import { useTranslation } from 'react-i18next';
import './contact.scss';
import React from 'react';
import axios from 'axios';

function Contact() {
    const { t } = useTranslation();

    const handleSubmit = async (event) => {
        event.preventDefault();

        const formData = new FormData(event.target);

        try {
            const response = await axios.post('/phpmailer/sendmail.php', formData);
            console.log('Response:', response.data);
            //     
        } catch (error) {
            console.error('Error:', error);
            //    
        }
    };
    return (
        <div className='wrapper'>
            <div className='container'>
                <div className='block-contact'>
                    <div className='contact-tittle'>
                        <p className='item animation_item hide'>{t("contact")}</p>
                    </div>
                        <div className="contact-info">
                            <form id='form' onSubmit={handleSubmit}>
                                <div className="contact-about fs-4">
                                    <p className='mt-3 fw-bolder item-name animation_item hide'>{t("name")}<br></br>
                                        <label htmlFor='formName'></label>
                                        <input type="text" id='formName' name='name' className="line _req" placeholder= {t("your_name")}></input>
                                    </p>
                                    <p className='mt-3 fw-bolder item-mail animation_item hide'>{t("email")}<br></br>
                                        <label htmlFor='formMail'></label>
                                        <input type="textl" id='formMail' name='email' className="line _req _email" placeholder= {t("your_mail")}></input>
                                    </p>
                                </div>
                                <div className="contact-textarea fs-4 mt-5 fw-bolder">
                                    <p className='item animation_item hide msg'>{t("message")}</p>
                                    <label htmlFor='formText'></label>
                                    <textarea id='formText' name='message' className="line item animation_item hide _req" placeholder={t("your_message")}></textarea>
                                </div>
                                <div className="contact-send mt-5">
                                    <button type="submit" value="Send Email" className='fw-bold animation_item hide'>{t("submit")}</button>
                                </div>
                            </form>
                        </div>
                </div>
            </div>
        </div>
    );     
}
export default Contact;